.App {
  font-family: sans-serif;
  text-align: center;
}
.ulSlides {
}
.ulSlides li {
  display: none;
}
.ulSlides li.active {
  display: block;
}
/*ulDots*/
.ulDots {
  list-style: none;
}
.ulDots li {
  border-radius: 50%;
  display: inline-block;
  width: 20px;
  margin: 0 2px;
  background: grey;
  cursor: pointer;
}
.ulDots li.active {
  background: green;
  color: #fff;
}
button.active{
  background: green;
  color: #fff;
}